.window {
  width: 100%;
  height: 100%;
  background: #333;
  color: #FFF;
  overflow: hidden;

  .content {
    height: 100%;
    overflow: scroll;
    transition: all .2s ease;

    &.dark {
      background: #000; } }

  .frame {
    background: #EEE;
    padding: .8em;
    display: none;

    .btn {
      height: 1em;
      width: 1em;
      border-radius: 50%;
      display: inline-block;
      background: #888;
      margin-right: 0.5em;

      &.btn-close {
        background: #FF605C; }
      &.btn-minimize {
        background: #FFBD44; }
      &.btn-fullscreen {
        background: #00CA4E; } } }

  @media screen and (min-width: 600px) {
    height: auto;
    width: 80%;
    max-width: 700px;
    border-radius: .4em;
    box-shadow: 0 2em 4em rgba(#000, .6);

    .frame {
      display: flex;

      .content {
        height: 40vh;
        max-height: 600px; } } } }
