@import url(https://cdnjs.cloudflare.com/ajax/libs/meyer-reset/2.0/reset.min.css);
@import url(https://cdnjs.cloudflare.com/ajax/libs/animate.css/3.7.2/animate.min.css);
html{font-family:monospace;font-size:20px}html,body,main{height:100%}main{background:#F75F45;display:flex;flex-direction:column;align-items:center;justify-content:center}*::-webkit-scrollbar{position:absolute;width:1em}*::-webkit-scrollbar-track{box-shadow:inset 0 0 6px rgba(0,0,0,0.3)}*::-webkit-scrollbar-thumb{background-color:darkgrey}

.editor{padding:2em;line-height:1.3;-moz-tab-size:2;tab-size:2}.editor pre{display:inline-block}.editor ::selection{background:rgba(255,255,255,0.2)}.editor .line{white-space:nowrap}.editor .comment{color:#777777}.editor .tag{color:#F07178}.editor .id{color:#F78C6C}.editor .selector{color:#FFCB6B}.editor .pseudo{color:#C792EA}.editor .spec,.editor .val{color:#89DDFF}.editor .prop{color:#85a4b1}.editor input{display:inline-block;margin:0;padding:0 0.2em;background:rgba(255,255,255,0.05);border:none;border-bottom:1px dashed;outline:none;line-height:inherit;color:inherit;font-size:inherit;font-family:inherit;transition:all .2s ease}.editor input:hover,.editor input:focus{background:rgba(255,255,255,0.2)}

.window{width:100%;height:100%;background:#333;color:#FFF;overflow:hidden}.window .content{height:100%;overflow:scroll;transition:all .2s ease}.window .content.dark{background:#000}.window .frame{background:#EEE;padding:.8em;display:none}.window .frame .btn{height:1em;width:1em;border-radius:50%;display:inline-block;background:#888;margin-right:0.5em}.window .frame .btn.btn-close{background:#FF605C}.window .frame .btn.btn-minimize{background:#FFBD44}.window .frame .btn.btn-fullscreen{background:#00CA4E}@media screen and (min-width: 600px){.window{height:auto;width:80%;max-width:700px;border-radius:.4em;box-shadow:0 2em 4em rgba(0,0,0,0.6)}.window .frame{display:flex}.window .frame .content{height:40vh;max-height:600px}}

